.hdrcart-preview {
  display: none;
  background: white;
  border: 1px solid gray-eighty;
} 

.hdrcart-preview::before {
    content: "";
    position: absolute;
    display: block;
    top: -9px;
    right: 8px;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 8px solid gray-eighty;
    z-index: 1;
  }

  .hdrcart-preview::after {
    content: "";
    position: absolute;
    display: block;
    top: -7px;
    right: 9px;
    width: 0;
    height: 0;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid white;
    z-index: 1;
  }

.hdrcart-preview.active {
  background: white;  
  z-index: 1;
  display: block;
  position: absolute;
  top: 5em;
  left:31.5em;
  width: 28%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.cart-rows{
  display: inline-block;
  width:20em;
}

