slider {
  display:block;
  width:100%;
  height:100%;
 /* margin-top: 5em; */
}

slider > *{
  position: absolute;
  display: block;
  width:100%;
  height:90%;
  -webkit-animation: slide 12s infinite;
          animation: slide 12s infinite;
  overflow: hidden;
}

slide:nth-child(1){
  left:0%;
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
  background-image: url(/lifyse/static/media/i2.c35fbeeb.jpg);
  background-size: cover;
  background-position: center;
}

slide:nth-child(2){
  left:0%;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  background-image: url(/lifyse/static/media/i5.b75d7b41.jpg);
  background-size: cover;
  background-position: center;
}

slide:nth-child(3){
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
  background-image: url(/lifyse/static/media/i10.758409e2.jpg);
  background-size: cover;
  background-position: center;
}
slide:nth-child(4){
  left:0%;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
  background-image: url(/lifyse/static/media/I11.f70d4c83.jpg);
  background-size: cover;
  background-position: center;
}
slide p {
  font-family: comfortaa;
font-size: 50px;
text-align:center;
display:grid;
width: 100%;
margin-top:200px;
color: #fff;
}
slide a{
  font-family: comfortaa;
  font-size: 30px;
  text-align:center;
  display:grid;
  width: 100%;
  color: #fff;
}

@-webkit-keyframes slide{
  0%{left:100%;width:100%;}
  5%{left:0%;}
  25%{left:0%;}
  30%{left:-100%;width:100%;}
  30.0001%{left:-100%;width:0%;}
  100%{left:100%;width:0%;}
}

@keyframes slide{
  0%{left:100%;width:100%;}
  5%{left:0%;}
  25%{left:0%;}
  30%{left:-100%;width:100%;}
  30.0001%{left:-100%;width:0%;}
  100%{left:100%;width:0%;}
}
